<template>
  <div>
    <component v-bind:is="commont"> </component>
  </div>
</template>

<script>
import allocationDefault from "./allocation_default";
import allocationBranch from "./allocation_branch";
import allocationSearch from "./allocation_search";
import { mapActions, mapGetters } from "vuex";

export default {
  components: {
    allocationDefault,
    allocationBranch,
    allocationSearch
  },
  data() {
    return {
      commont: "allocationDefault"
    };
  },
  computed: {
    ...mapGetters([
      "allocationDefault_showGet",
      "allocationBranch_showGet",
      "allocationSearch_showGet"
    ])
  },
  watch: {
    allocationDefault_showGet() {
      this.commont = this.allocationDefault_showGet.commont;
    },
    allocationBranch_showGet() {
      this.commont = this.allocationBranch_showGet.commont;
    },
    allocationSearch_showGet() {
      this.commont = this.allocationSearch_showGet.commont;
    }
  }
};
</script>

