<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="采购组织" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="采购部门" size="mini">
          <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @change="departmentChange">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList.options"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="单据号" size="mini">
          <el-input v-model="params.instorage_sn" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="供应商(厂家)" size="mini">
          <el-select clearable v-model="params.supplier_id" maxlength="100" :filter-method="supplierChange" @focus="supplier" filterable placeholder="请选择">
            <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品名称" size="mini">
          <el-input v-model="params.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="商品编号" size="mini">
          <el-input v-model="params.goods_sn"></el-input>
        </el-form-item>
        <el-form-item label="仓库名称" size="mini">
          <el-select clearable v-model="params.warehouse_id" filterable placeholder="请选择" @focus="getWarehouse" @change="getAllocation">
            <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList.options"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="货位名称" size="mini">
          <el-select clearable v-model="params.allocation_id" filterable placeholder="请选择">
            <el-option :label="item.allocation_name" :value="item.allocation_id" :key="item.allocation_id" v-for="item in allocationList.options"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="货位分配状态" size="mini">
          <el-select clearable v-model="params.auditor_status">
            <el-option label="全部" :value="2">全部</el-option>
            <el-option label="待分配" :value="0">待分配</el-option>
            <el-option label="已分配" :value="1">已分配</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="入库类型" size="mini">
          <el-select clearable v-model="params.type">
            <el-option label="采购入库" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>

      <div class="add_delet">
        <el-button plain @click="branch" class="addBtn" size="mini" v-if="authority.b_allot==1">分配货位</el-button>
      </div>
    </div>

    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="采购组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" min-width="170" label="商品编号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="商品名称">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" width="120" label="年周/批次"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="supplier_name" width="150" label="供应商(厂家)"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" width="90" label="入库数量" align="right"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" width="132" label="仓库"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" width="180" label="货位"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_num" width="180" label="货位分配数量"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="is_allot" width="120" label="货位分配状态"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_time" width="180" label="业务日期"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="instorage_add_time" width="170" label="单据时间"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="instorage_sn" width="180" label="单据号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="type" width="70" label="入库类型"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="部门"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" width="180" label="备注"></el-table-column>

      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="10" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import {
  getData,
  getCompanyListCommon,
  getDepartmentListCommon,
  supplierListOpen //获取供应商下拉
} from "common/utils/public_function/commonUtils";
export default {
  name: "purchaseAllocationdefault",
  data() {
    return {
      authority: {},
      timeValue: [],
      params: {
        auditor_status: 0
      },
      customerList: {
        options: []
      },
      departmentList: {
        options: []
      },
      companyList: [],
      supplierList: [],
      // supplier: { options: [] },
      warehouseList: {
        options: []
      },
      allocationList: {
        options: []
      },
      TableData: {
        tableData: [],
        total: 10
      },
      per_page: [30, 50, 100, 500],
      pageSize: 30,
      multipleSelection: [],
      goPage: 1, //默认搜索页面
      searchParams: {}
    };
  },
  methods: {
    ...mapActions(["allocationBranch_show"]),
    departmentChange() {
      this.$set(this.params, "warehouse_id", "");
    },
    getWarehouse() {
      this.$common.options("getWarehouseListOption", this.warehouseList, {
        company_id: this.params.company_id,
        department_id: this.params.department_id
      }); //仓库管理列表
    },
    onReset() {
      this.timeValue = [];
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        auditor_status: 0
      });
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "purchaseGetGoodsPlacementList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
    },
    supplierChange(val) {
      let options = {
        company_id: this.params.company_id,
        supplier_name: val
      };
      supplierListOpen(this, options);
    },
    onSearch() {
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams);
    },
    supplier() {
      let options = {
        company_id: this.params.company_id,
        supplier_name: null
      };
      supplierListOpen(this, options);
    },
    companyChange(val) {
      this.$set(this.departmentList, "options", "");
      if (val != "") {
        getDepartmentListCommon(this, this.params, 1);
      }
      this.$set(this.params, "supplier_id", "");
      this.$set(this.params, "department_id", "");
      this.$set(this.params, "warehouse_id", "");
      this.$set(this.departmentList, "options", "");
    },
    branch() {
      if (this.multipleSelection.length) {
        this.allocationBranch_show({
          commont: "allocationBranch",
          instorage_goods_id: this.multipleSelection.map(
            item => item.instorage_goods_id
          )
        });
      } else {
        this.$message({
          message: "请先选择要分配的货位！",
          type: "warning"
        });
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    },
    getAllocation(val) {
      // 仓库和货位二级联动
      this.allocationList.options = [];
      this.$set(this.params, "allocation_id", "");
      this.$common.options("getAllocationListOption", this.allocationList, {
        warehouse_id: val
      });
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "params", {
      auditor_status: 0
    });
    getCompanyListCommon(this); //获取组织

    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "purchaseGetGoodsPlacementList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
    this.$common.options("getSupplierListOption", this.supplier);
    this.$common.options("getWarehouseListOption", this.warehouseList, {
      company_id: this.params.company_id
    });

    // this.$common.options("getAllocationListOption", this.allocationList);
  }
};
</script>

