<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">货位分配</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>分配货位</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <div class="add_delet">
        <el-button plain @click="deleteF" class="addAlloction" size="mini">删行</el-button>
      </div>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" min-width="180" label="库存组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="instorage_sns" min-width="170" label="单据号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" width="160" label="商品编号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="商品名称">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" width="120" label="年周/批次"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="supplier_name" width="150" label="供应商(厂家)"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" width="90" label="入库数量" align="right"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" width="132" label="仓库"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_id" width="180" label="货位">
          <template slot-scope="scope">
            <el-select class="el_slect" v-model="scope.row.allocation_id" placeholder="请选择" size="mini">
              <el-option :label="item.allocation_name" :value="item.allocation_id" :key="item.allocation_id" v-for="item in scope.row.allocationInventoryList"></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_num" width="180" label="货位分配数量" align="right"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" width="180" label="备注"></el-table-column>
      </el-table>
    </div>

    <el-dialog :close-on-click-modal="false" title="新增仓库类型" center :visible.sync="dialogFormVisible" width="450px">
      <el-form :model="ruleForm" ref="ruleForm">
        <el-form-item label="所属仓库">
          {{warehouse_name}}
        </el-form-item>
        <el-form-item label="货位名称" prop="allocation_name" :rules="rules.allocation_name">
          <el-input v-model="ruleForm.allocation_name" style="width:250px;"></el-input>
        </el-form-item>
        <el-form-item label="备注" size="medium">
          <el-input type="text" maxlength="300" style="width:250px;" v-model="ruleForm.remark"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormsubmit('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>

    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack()">取 消</el-button>
      <el-button type="primary" @click="submitForm()">确 定</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "purchaseAllocationbranch",
  data() {
    return {
      authority: {},
      pageSize: 30,
      goPage: 1, //默认搜索页面

      params: {},
      instorage_goods_id: null,
      TableData: [],
      per_page: [30, 50, 100, 500],
      goPage: 1,
      pageSize: 30,
      dialogFormVisible: false,
      ruleForm: {
        warehouse_id: ""
      },
      searchParams: {},
      warehouse_name: "",
      multipleSelection: []
    };
  },
  methods: {
    ...mapActions(["allocationDefault_show"]),
    goBack() {
      this.allocationDefault_show({
        commont: "allocationDefault"
      });
    },
    initData() {
      this.$axios({
        method: "get",
        url: this.$api.purchaseInfoGoodsPlacement,
        params: this.searchParams
      })
        .then(({ data }) => {
          if (data.code == 200) {
            this.$set(this, "TableData", data.data);
            return data;
          }
        })
        .then(data => {
          for (let i = 0, len = data.data.length; i < len; i++) {
            this.$axMethod
              .allocationInventoryList({
                warehouse_id: data.data[i].warehouse_id
              })
              .then(res => {
                this.$set(
                  this.TableData[i],
                  "allocationInventoryList",
                  res.data.data
                );
              });
          }
        });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    deleteF() {
      if (this.multipleSelection.length) {
        let a = new Set(this.TableData);
        let b = new Set(this.multipleSelection);
        let differenceABSet = new Set([...a].filter(x => !b.has(x)));
        this.TableData = Array.from(differenceABSet);
      } else {
        this.$message({
          message: "请先选择要删除的数据！",
          type: "warning"
        });
      }
    },
    dialogFormsubmit(ruleForm) {
      this.$refs[ruleForm].validate(valid => {
        if (valid) {
          this.$axios({
            method: "post",
            url: this.$api.addAllocation,
            data: this.ruleForm
          }).then(({ data }) => {
            if (data.code == 200) {
              this.dialogFormVisible = false;
              this.$message({
                message: data.data,
                type: "success"
              });
            }
          });
        } else {
          console.error("error submit!!");
          return false;
        }
      });
    },
    submitForm() {
      let flag = true;
      this.TableData.forEach(item => {
        if (item.allocation_num > item.goods_num) {
          this.$message.warning("货位分配数量不得大于入库数量");
          flag = false;
          return false;
        }
      });
      if (!flag) return false;
      let placement_info = JSON.stringify(
        this.TableData.map(item => {
          return {
            instorage_goods_id: item.instorage_goods_id,
            allocation_id: item.allocation_id
          };
        })
      );
      this.$axMethod
        .purchaseAddGoodsPlacement({
          placement_info: placement_info
        })
        .then(({ data }) => {
          if (data.code == "200") {
            this.$message.success(data.data);
            this.goBack();
          }
        });
    }
  },
  computed: {
    ...mapGetters(["allocationBranch_showGet"])
  },
  mounted() {
    this.$set(
      this.params,
      "instorage_goods_id",
      this.allocationBranch_showGet.instorage_goods_id.join(",")
    );
    this.searchParams = Object.assign({}, this.params);
    this.initData();
  }
};
</script>

