<template>
  <div class="search_container">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">货位分配</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看货位</el-breadcrumb-item>
    </el-breadcrumb>
    <div>
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="销售组织:" size="mini">
          {{TableData.infoList.company_name}}
        </el-form-item>
        <el-form-item label="仓库名称:" size="mini">
          {{TableData.infoList.warehouse_name}}
        </el-form-item>
        <el-form-item label="商品名称:" size="mini">
          {{TableData.infoList.goods_name}}
        </el-form-item>
        <el-form-item label="批次:" size="mini">
          {{TableData.infoList.batch}}
        </el-form-item>
        <el-form-item label="数量:" size="mini">
          {{TableData.infoList.num}}
        </el-form-item>
      </el-form>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%">
          <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位名称"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="allot_num" label="分配时货位数量" align="right"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="now_num" label="货位分配数量" align="right"></el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">返 回</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      authority: {},
      instorage_goods_id: null,
      TableData: {
        infoList: {},
        tableData: [],
        per_page: [30, 50, 100, 500],
        total: 10
      },
      per_page: [30, 50, 100, 500],
      pageSize: 30,
      goPage: 1 //默认搜索页面
    };
  },
  methods: {
    handleSizeChange(val) {
      let vm = this;
      this.$axios({
        method: "get",
        url: vm.$api.seekGoodsPlacement,
        params: {
          instorage_goods_id: this.instorage_goods_id,
          per_page: val
        }
      }).then(({ data }) => {
        if (data.code == 200) {
          this.$set(this, "TableData", data.data.data);
          this.TableData.tableData = data.data.data.data;
          this.TableData.infoList = data.data.goodsInfo;
        }
      });
    },
    handleCurrentChange(val) {
      let vm = this;
      this.$axios({
        method: "get",
        url: vm.$api.seekGoodsPlacement,
        params: {
          instorage_goods_id: this.instorage_goods_id,
          page: val
        }
      }).then(({ data }) => {
        if (data.code == 200) {
          this.$set(this, "TableData", data.data.data);
          this.TableData.tableData = data.data.data.data;
          this.TableData.infoList = data.data.goodsInfo;
        }
      });
    },
    ...mapActions(["allocationDefault_show"]),
    goBack() {
      this.allocationDefault_show({ commont: "allocationDefault" });
    }
  },
  computed: {
    ...mapGetters(["allocationSearch_showGet"])
  },
  created() {
    let vm = this;
    this.$axios({
      method: "get",
      url: vm.$api.seekGoodsPlacement,
      params: {
        instorage_goods_id: this.allocationSearch_showGet.instorage_goods_id
      }
    }).then(({ data }) => {
      if (data.code == 200) {
        this.$set(this, "TableData", data.data.data);
        this.TableData.tableData = data.data.data.data;
        this.TableData.infoList = data.data.goodsInfo;
      }
    });
    this.instorage_goods_id = this.allocationSearch_showGet.instorage_goods_id;
  }
};
</script>

